import React from 'react'

export default function Shop() {
    return (
        <section className='bg-white py-3 lg:py-10 overflow-hidden'>

            <div className='py-3 lg:py-20 mt-1 relative'>

                <div className='container grid grid-cols-12 gap-8 relative z-10'>

                    <div className='col-span-12 lg:col-span-7 flex flex-col justify-center order-1 lg:order-[initial] text-center lg:text-start'>

                        <button className='w-fit px-20 hidden lg:block py-2 text-[40px] text-white uppercase' style={{ fontFamily: "Moore", letterSpacing: "2px", background: `url(${require('./btn-bg.png')})`, backgroundPosition: 'center', backgroundSize: '100%  90%', backgroundRepeat: "no-repeat" }}>Shop</button>

                        <h1 className='text-[40px] font-[400] text-[#58291E] mb-4 mt-10'>

                            No te preocupes por el <span className='text-[#45B8EA]' style={{ fontFamily: "myFont" }}>SEO</span>, no hagas cuentas con<span className='mx-2 text-[#45B8EA] font-normal' style={{ fontFamily: 'myFont' }}>Google Add</span>

                        </h1>

                        <h1 className='text-[28px] text-[#58291E] font-[400] mb-8'>
                            Cafés
                            En nuestra shop los distintos <span className='mx-1 text-[#45B8EA] font-normal' style={{ fontFamily: 'myFont' }}>cafés</span> se posicionarán de manera orgánica a través de las valoraciones de los usuarios, siempre primando el consumo de proximidad.
                        </h1>

                        <h1 className='text-[28px] text-[#58291E] font-[400] mb-4'>

                            No somos intermediarios, tus ventas son directas a tu cliente, solo aportamos las herramientas de automatización logística y de pago.

                        </h1>

                    </div>

                    <div className='col-span-12 lg:col-span-5 flex items-end justify-center flex-col'>


                        <button className='bg-[#45B8EA] w-fit text-white px-20 py-2 mb-10 block lg:hidden rounded-[25px] text-[30px] -rotate-3 font-bold uppercase'>shop</button>


                        <img src={require('./row-bg.png')} className='w-[325px]' />

                    </div>

                </div>

            </div>

        </section>
    )
}
