import React from 'react'

export default function Subscriptions() {
    return (
        <section className='bg-white py-3 lg:py-10 overflow-hidden'>

            <div className='py-3 lg:py-20 mt-1 relative'>

                <img src={require('../../../../assests/images/sun.png')} alt='' className='absolute -right-32 bottom-[-200px]' />

                <div className='container grid grid-cols-12 gap-8 relative z-10'>

                    <div className='col-span-12 lg:col-span-5 flex items-start justify-center flex-col'>


                        <button className='bg-[#45B8EA] w-fit text-white px-5 sm:px-20 py-2 mb-10 block lg:hidden rounded-[25px] text-[30px] -rotate-3 font-bold uppercase'>subscriptions</button>


                        <img src={require('./row-bg.png')} className='w-[325px] max-w-full' />

                    </div>

                    <div className='col-span-12 lg:col-span-7 flex flex-col justify-center text-center lg:text-start'>

                        <button className='w-fit px-20 hidden lg:block py-2 text-[40px] text-white uppercase' style={{ fontFamily: "Moore", letterSpacing: "2px", background: `url(${require('./btn-bg.png')})`, backgroundPosition: 'center' , backgroundSize: 'contain' , backgroundRepeat: "no-repeat" }}>subscriptions</button>

                        <h1 className='text-[40px] font-[400] mb-4 mt-10 text-[#3F2621]'>

                            Genera o mejora tus ofertas de suscripción con <span className='mx-2 text-[#45B8EA] font-[400]' style={{ fontFamily: 'myFont' }}>Rovle</span>

                        </h1>

                        <h1 className='text-[28px] text-[#58291E] font-[400] mb-4'>

                            Automatiza los cobros y deja que tus clientes decidan si quieren que se lo enviemos a casa o que lo recojan en tu establecimiento.

                        </h1>

                    </div>


                </div>

            </div>

        </section>
    )
}
