import { faArrowDown } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React from 'react'

export default function Hero() {
    return (
        <section className='bg-[#583835] pt-10'>

            <div className='container grid grid-cols-12 gap-5 bg-[#583835]'>

                <div className='relative col-span-12 lg:col-span-6 flex flex-col items-center justify-center'>

                    <img src={require('../../../../assests/images/hero-one.png')} alt='' className='' />

                    <div className='flex flex-col lg:flex-row justify-start items-center w-full lg:ms-10 mt-10 mb-20 lg:mb-32 '>

                        <button className='bg-[#43B8EA] flex items-center text-[20px] text-white font-medium mb-5 lg:mb-0 me-0 lg:me-3 p-4 px-16 rounded-[30px] '>

                            <FontAwesomeIcon icon={faArrowDown} />

                            <span className='ms-3'>
                                Descargar App
                            </span>

                        </button>

                        <button className='p-4 px-16 bg-transparent text-[#43B8EA] border-2 border-[#43B8EA] font-medium rounded-[30px]'>
                            Contacto
                        </button>

                    </div>

                </div>

                <div className='relative col-span-6 hidden lg:flex items-start justify-end'>

                    <img src={require('../../../../assests/images/hero-two.png')} alt='' className='h-[700px]' />

                </div>

            </div>



        </section>
    )
}
