import React from 'react'

export default function Passaport() {
    return (
        <section className='bg-white py-3 lg:py-10'>

            <div className='container grid grid-cols-12 gap-5 lg:gap-10 relative'>

                <div className='col-span-12 lg:col-span-6 mb-48'>

                    <h1 className='text-[25px] font-[400] text-[#58291E]'>

                        A través de nuestra <span className='text-[#45B8EA] font-[400]' style={{ fontFamily: "myFont" }}>APP</span> vais a conseguir mejorar la captación, fidelización y comunicación con vuestros clientes, tanto a través de sus móviles como en vuestros establecimientos

                    </h1>

                </div>

                <div className='hidden lg:block absolute left-[49%] translate-x-[-50%] top-0 w-[3px] h-[40%] bg-[#45B8EA]'></div>
                <div className='lg:hidden absolute left-0 top-[50%] translate-y-[-50%]  w-full h-[2px] bg-[#45B8EA]'></div>

                <div className='col-span-12 lg:col-span-6'>

                    <h1 className='text-[25px] font-[400] text-[#58291E]'>

                        No somos simplemente un Marketplace.
                        <img src={require('./text-one.PNG')} className='inline' /> es una plataforma para encontrar a todos los amantes del café de especialidad y crecer juntos

                    </h1>

                </div>

            </div>

            <div className='py-20 mt-1 relative'>

                <img src={require('../../../../assests/images/sun.png')} alt='' className='absolute left-0 top-[-100px]' />

                <div className='container grid grid-cols-12 gap-8 relative z-10'>

                    <div className='col-span-12 lg:col-span-7 flex flex-col  items-center lg:items-start justify-center order-1 lg:order-[initial] '>

                        <button className='w-fit px-20 hidden lg:block py-2 text-[40px] text-white uppercase' style={{ fontFamily: "Moore", letterSpacing: "2px", background: `url(${require('./btn-bg.png')})`, backgroundRepeat: "no-repeat", backgroundSize: 'contain', backgroundPosition: 'center' }}>passaport</button>

                        <h1 className='text-[40px] font-[400] text-[#58291E] mb-4 mt-10 text-center lg:text-start'>

                            El<span className='mx-2 text-[#45B8EA] font-[400]' style={{ fontFamily: 'myFont' }}>pasaporte</span> es una tarjeta de fidelización digital

                        </h1>

                        <h1 className='text-[28px]  font-[400] text-[#58291E] mb-8 text-center lg:text-start'>

                            Un <span className='text-[#45B8EA]' style={{ fontFamily: "myFont" }}>QR</span> asociado a cada usuario registrará todas las compras que haga en tu establecimiento.

                        </h1>

                        <h1 className='text-[28px] font-[400] text-[#58291E] mb-1 text-center lg:text-start'>

                            Cada vez que un cliente “selle” en un establecimiento este aparecerá en su pasaporte, teniendo así acceso a sus<span className='mx-2 text-[#45B8EA] font-[400]' style={{ fontFamily: 'myFont' }}>ofertas</span>  y promociones.

                        </h1>

                    </div>

                    <div className='col-span-12 lg:col-span-5 flex items-center justify-end flex-col'>

                        <button className='bg-[#45B8EA] w-fit text-white px-20  mb-10 block lg:hidden py-2 rounded-[25px] text-[30px] -rotate-3 font-[400]  uppercase'>passaport</button>

                        <img src={require('./row-bg.png')} className='w-[325px] max-w-full' />

                    </div>

                </div>

            </div>

        </section>
    )
}
